@import url('https://fonts.googleapis.com/css2?family=Karla&display=swap');
.borderbox {
    width: 60%;
    padding: 10px 15px;
    margin: 8px 0;
    border-color: #3e7643;
    border: 1px solid #fac922;
    background-color: #fad763;
    display: inline-block;
    box-sizing: border-box;
    border-radius: 3%;
}
.scheduletable{
    color: white;
    font-family: 'Karla', sans-serif;
}
#selectbutton{
    width: 15%;
    margin: 3px 3px;
    padding:.1em;
}

select{
    width:20%;
    margin:3px 3px;
    font-family: 'Karla', sans-serif;
    font-size: large;
    color: white;
    background-color: #004e42;
}

Table, td, th{
    border: 1px solid black;
    font-size: x-large;
}
Table{
    width: 100%;
    
}
tr:nth-child(even){
    color:white;
    background-color: #a2a569;
}
TableRow:nth-child(odd){
    color: white;
    background-color: #c6893f;
}
th{
    background-color: #004e42;
}

.sem-select{
    display: flex;
    justify-content: space-around;
}
.leftright{
    color:white;
}

#prev{
    width: 200px;
}